.soundboard-container {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.controls-container {
  gap: 20px;
  margin-bottom: 20px;
  display: flex;
}

.stop-button {
  color: #fff;
  cursor: pointer;
  background-color: #ff4d4d;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
}

.stop-button.active {
  background-color: #c00;
}

.volume-control {
  align-items: center;
  gap: 10px;
  display: flex;
}

.volume-slider {
  width: 100px;
}

.tabs-container {
  margin-bottom: 20px;
}

.tab {
  cursor: pointer;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 5px;
  padding: 8px 12px;
}

.tab.active {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}

.sounds-container {
  flex-wrap: wrap;
  gap: 15px;
  display: flex;
}

.sound-item {
  cursor: pointer;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 150px;
  padding: 10px;
}

.sound-name {
  margin-bottom: 8px;
  font-weight: bold;
}

.sound-actions {
  gap: 10px;
  display: flex;
}

.favorite-button.active {
  color: red;
}

.sidebar-container {
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 30px;
  display: flex;
}

.sidebar-button {
  cursor: pointer;
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px 12px;
}

.sidebar-button.active {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}

.room-info {
  margin-top: 20px;
  font-weight: bold;
}

.home-container {
  text-align: center;
  padding: 40px;
}

.room-actions {
  margin-top: 20px;
}

.create-room-button {
  cursor: pointer;
  padding: 10px 20px;
  font-size: 16px;
}

.join-room-form input {
  width: 200px;
  margin-right: 10px;
  padding: 8px;
  font-size: 16px;
}

.join-room-form button {
  cursor: pointer;
  padding: 8px 16px;
  font-size: 16px;
}
